/**
 * Âà§Êñ≠Â≠óÁ¨¶‰∏≤ÊòØÂê¶‰∏∫ÂêàÊ≥ï JSON Ê†ºÂºè
 */
export const isValidJson = (str: unknown): boolean => {
    if (typeof str !== 'string') return false;
    try {
        JSON.parse(str);
        return true;
    } catch {
        return false;
    }
};

/**
 * Ê†πÊçÆË∑ØÂæÑÔºàÂ¶Ç 'a.1.b'Ôºâ‰øÆÊîπÂØπË±°‰∏≠ÁöÑÂØπÂ∫îÂÄº
 * @param obj Ê∫êÂØπË±°
 * @param path ÁÇπÂàÜË∑ØÂæÑÂ≠óÁ¨¶‰∏≤
 * @param value Êñ∞ÂÄº
 */
export const updateValueByPath = (obj: any, path: string, value: string | string[] | boolean) => {
    if (!obj || !path || value === void 0) return;
    // if (!obj || !path || !value) return;
    const newObj = JSON.parse(JSON.stringify(obj));

    const keys = path.split('.');
    let current = newObj;
    for (let i = 0; i < keys.length - 1; i++) {
        const key = isNaN(Number(keys[i])) ? keys[i] : Number(keys[i]);
        if (!current[key]) {
            throw new Error(`Property not found: ${keys[i]}`);
        }
        current = current[key];
    }
    //ÊúÄÂêé‰∏ÄÈ°πÂçïÊãéÂá∫Êù•ËøõË°åËµãÂÄºÊìç‰Ωú
    // const finalKey = isNaN(Number(keys.at(-1))) ? keys.at(-1) : Number(keys.at(-1)); Êèí‰ª∂Á´ØÊµèËßàÂô®ÁâàÊú¨Ëøá‰Ωé‰∏çÊîØÊåÅatËØ≠Ê≥ï
    const finalKey = isNaN(Number(keys[keys.length - 1]))
        ? keys[keys.length - 1]
        : Number(keys[keys.length - 1]);
    current[finalKey!] = value;

    return newObj;
};



export const findAllAssertKeys = (fData: any) => {
    // Êî∂ÈõÜÊâÄÊúâÊª°Ë∂≥Êù°‰ª∂ÁöÑÈîÆ
    try {

        const keys: any[] = [];
        const collectKeys = (data: any, prefix = '') => {
            if (Array.isArray(data)) {
                data.forEach((el, index) => {
                    const key = `${prefix}.${index}`;
                    if (typeof el === 'object' && el !== null) {
                        collectKeys(el, key);
                    } else {
                        keys.push(key);
                    }
                });
            }
            if (data !== null && typeof data === 'object') {
                Object.keys(data).forEach((key) => {
                    const el = data[key];
                    const fullKey = prefix ? `${prefix}.${key}` : key;
                    if (typeof el === 'object' && el !== null) {
                        collectKeys(el, fullKey);
                    } else {
                        keys.push(fullKey);
                    }
                });
            }
        };

        collectKeys(fData);

        return keys;
    } catch (error) {
        console.error(error);
    }
};


export const deepConvertEmptyArraysToString = (data: any): any => {
    if (Array.isArray(data)) {
        return data.length === 0 ? '[]' : data.map(deepConvertEmptyArraysToString);
    }
    if (data !== null && typeof data === 'object') {
        return Object.fromEntries(
            Object.entries(data).map(([key, value]) => [key, deepConvertEmptyArraysToString(value)]),
        );
    }
    return data;
};

/**
 * JSONPath ËΩ¨ ÈìæÂºèË∑ØÂæÑ
 * @example
 * convertJsonPathToChainPath(['$[0].a.b'], 'root')
 * => ['root.0.a.b']
 */
export const convertJsonPathToChainPath = (paths: string[], rootNode: string): string[] => {
    if (!Array.isArray(paths)) return [];

    return paths.map((path) => {
        let res = path.replace(/^\$/, ''); // ÂéªÊéâ $
        res = res.replace(/\[(\d+)\]/g, '.$1'); // [0] -> .0
        if (res.startsWith('.')) res = res.slice(1);
        return `${rootNode}.${res}`;
    });
};



/**
 * ÈìæÂºèË∑ØÂæÑ ËΩ¨ JSONPath
 * @example
 * convertChainPathToJsonPath(['root.0.a.b'])
 * => ['$[0].a.b']
 */
export const convertChainPathToJsonPath = (paths: string[]): string[] => {
    if (!Array.isArray(paths)) return [];
    return paths.map((path) => {
        const trimmed = path.replace(/^[^.]+/, ''); // ÂéªÊéâ root
        const res = trimmed.replace(/\.(\d+)/g, '[$1]'); // .0 -> [0]
        return `$${res}`;
    });
};