import ConfigSpecialVerif from './ConfigSpecialVerif';
import EditValue from './EditValue';
import React, { useEffect, useMemo, useState } from 'react';
import { Checkbox, Tree } from 'antd';
/* eslint-disable react-hooks/exhaustive-deps */
import {
    findAllAssertKeys,
    updateValueByPath,
    deepConvertEmptyArraysToString,
    convertJsonPathToChainPath,
    convertChainPathToJsonPath,
} from '../utils';
interface CustomEditorType {
    ConvertToAntdTreeDataFunction: (data: any, nodeKey?: string, depth?: number) => any;
    BuildTreeNode: (el: object, key: any, isBasicType?: boolean) => void;
}
interface IProps {
    data: any;
    rootNode: string;
    showAssertChecked?: boolean;
    showEditValue?: boolean;
    isAllAssert?: boolean;
    isAllCheckbox?: boolean;
    onChange: (...set: any) => void;
    showNode?: any;
    noShowAssert?: boolean;
    showConfigSpecialVerif?: boolean
}
/**
 * @param showAssertChecked       ÊòØÂê¶Â±ïÁ§∫AssertÊ°ÜÔºàÈªòËÆ§‰∏çÂ±ïÁ§∫Ôºâ
 * @param showEditValue           ÊòØÂê¶Â±ïÁ§∫ÁºñËæëÊ°ÜÔºàÈªòËÆ§‰∏çÂ±ïÁ§∫Ôºâ
 * @param isAllAssert             ÊòØÂê¶Â±ïÁ§∫ÂÖ®ÈÄâÔºàÈªòËÆ§‰∏çÂ±ïÁ§∫Ôºâ
 * @param showConfigSpecialVerif  ÊòØÂê¶Â±ïÁ§∫ÁâπÊÆäÊ†°È™åËßÑÂàôÔºàÈªòËÆ§‰∏çÂ±ïÁ§∫Ôºâ
 * @param isAllCheckbox           ÊòØÂê¶ÂÖ®ÈÄâÔºàÈªòËÆ§‰∏çÂÖ®ÈÄâÔºâ
 * @param noShowAssert            ÊòØÂê¶Â±ïÁ§∫assertÊñáÂ≠óÔºàÈªòËÆ§Â±ïÁ§∫Ôºâ
 * @param showNode                ÊåáÂÆöËäÇÁÇπÁöÑvalueÂÄº‰Ωú‰∏∫Êï∞ÊçÆÊ∫ê
 * @returns
 */


const CustomEditor = (props: IProps) => {
    const {
        data: propsData,
        rootNode,
        showAssertChecked = false,
        showEditValue = false,
        isAllAssert = false,
        isAllCheckbox = false,
        onChange,
        noShowAssert,
        showConfigSpecialVerif = false,
    } = props;
    // console.log("üöÄ ~ CustomEditor ~ propsData:", propsData)
    const [editorData, setEditorData] = useState(propsData);
    const [renderData, setRenderData] = useState<any>();
    const [assertKeys, setAssertKeys] = useState<string[]>([]);
    const [allCheckedKeys, setAllCheckedKeys] = useState<any>([]);

    useEffect(() => {
        // console.log('CustomEditorsubData', editorData);
        onChange(editorData);
    }, [editorData]);

    const [isFirstRender, setIsFirstRender] = useState(true)
    useEffect(() => {
        //ÂàùÂßãÂåñ
        if (propsData) {
            const newRenderData = deepConvertEmptyArraysToString(editorData)
           
            delete newRenderData?.[rootNode].assertKeys
              delete newRenderData?.[rootNode].specialAssertConfigs
            setRenderData(newRenderData);
            if (isFirstRender) {
                const newAssertKeys = convertJsonPathToChainPath((propsData[rootNode]?.['assertKeys']), rootNode)
                setAssertKeys(newAssertKeys);//ÂõûÊòæÂãæÈÄâÈ°π
                setAllCheckedKeys(findAllAssertKeys(newRenderData));
                if (isAllCheckbox) {
                    selectAll(true, newRenderData)
                }

            }
            setIsFirstRender(false)
        }
    }, [propsData]);


    const updateEditorData = (newData: any, patch: string, val: string | string[]) => {
        const newSubData = updateValueByPath(newData, patch, val);
        setEditorData(newSubData);
    };

    /**ÁºñËæëËäÇÁÇπÊó∂ÂÄôÊõ¥Êñ∞Êï∞ÊçÆ */
    const onEdit = (key: string, val: string) => {
        updateEditorData(editorData, key, val);
    };

    /**ÂãæÈÄâÈ°π */
    const onCheckbox = (isChecked: boolean, selectTreeKey: string) => {
        let newAssertKeys = [...assertKeys];
        if (isChecked) {
            newAssertKeys.push(selectTreeKey);
        } else {
            newAssertKeys = newAssertKeys.filter((item) => item !== selectTreeKey);
        }
        setAssertKeys(newAssertKeys);
    };

    /**ÁªôÊï∞ÊçÆÊ†πËäÇÁÇπÔºàrootNodeÔºâÊ∑ªÂä†assertKeysÈ°π */
    const addAssertKeys = (keys: string[]) => {
        if (keys.length > 0) {
            updateEditorData(editorData, `${rootNode}.assertKeys`, convertChainPathToJsonPath(keys));
        } else {
            updateEditorData(editorData, `${rootNode}.assertKeys`, []);
        }
    };

    /**ÂÖ®ÈÄâAssert */
    const selectAll = (check: boolean, data?: any) => {
        console.log("üöÄ ~ selectAll ~ check:", check)
        if (check) {
            const keys = findAllAssertKeys(data || renderData);
            setAssertKeys(keys as string[]);
        } else {
            setAssertKeys([]);
        }
    };



    useEffect(() => {
        console.log('assertKeys', assertKeys);
        if (showAssertChecked) {
            addAssertKeys(assertKeys);
        }

    }, [assertKeys]);


    /**ÈÖçÁΩÆÁâπÊÆäÊ†°È™å
     * ‰∏ç‰∏éÂéüÈÄªËæëËÄ¶ÂêàÔºàÂè™ÈúÄ‰øÆÊîπÊ≠§ÂùóÔºâ
     * Ôºà‰ª£Á†ÅÂÅ•Â£ÆÊÄßÈ¢ÑÁïôÊãìÂ±ïÔºâ
     * Map ÁöÑÁâπÊÄßÔºö
     * Map ÊòØ‰∏ÄÁßçÈîÆÂÄºÂØπÊï∞ÊçÆÁªìÊûÑÔºåÂÆÉÁöÑÈîÆÊòØÂîØ‰∏ÄÁöÑ„ÄÇ
     * Â¶ÇÊûúÂ§öÊ¨°‰ΩøÁî®Áõ∏ÂêåÁöÑÈîÆÔºàitem.keyPathÔºâË∞ÉÁî® Map.set()ÔºåÂêé‰∏ÄÊ¨°ÁöÑÂÄº‰ºöË¶ÜÁõñÂâç‰∏ÄÊ¨°ÁöÑÂÄº„ÄÇ
     */
    const handleSpecialConfig = (vals: any) => {
        const specialAssertConfigs = editorData?.[rootNode]?.specialAssertConfigs ?? []

        //Ê†πÊçÆkeyPathÂéªÈáçÔºå‰øùÁïôÊúÄÊñ∞È°π 
        const resultMap = new Map();
        [...specialAssertConfigs, vals].forEach((item) => {
            resultMap.set(item.keyPath, item);
        });
        const uniqueArray = Array.from(resultMap.values());

        const newSubData = updateValueByPath(editorData, `${rootNode}.specialAssertConfigs`, uniqueArray);
        setEditorData(newSubData);
    }

    /**Êï∞ÊçÆËΩ¨‰∏∫treeÁªÑ‰ª∂Êï∞ÊçÆÊ†ºÂºè */
    const convertToAntdTreeData: CustomEditorType['ConvertToAntdTreeDataFunction'] = (
        data,
        nodeKey = 'root',
        depth = 0,
    ) => {
        // ÊûÑÂª∫Ê†ëËäÇÁÇπ
        const buildTreeNode: CustomEditorType['BuildTreeNode'] = (el, key, isBasicType = false) => {
            // Êõ¥Êñ∞ËäÇÁÇπkey‰∏∫ÈìæÂºèÁªìÊûÑ
            const itemKey = depth === 0 ? `${key}` : `${nodeKey}.${key}`;
            /**
             * Â≠êËäÇÁÇπ‰∏∫Âü∫Êú¨Êï∞ÊçÆÁ±ªÂûãÂπ∂‰∏îÊòØÂ≠êËäÇÁÇπÊâçÊîØÊåÅÂãæÈÄâÁºñËæë
             */
            const title = isBasicType ? (
                // isBasicType && itemKey.includes('yamlData') ? (
                <div style={{ display: 'flex' }}>
                    <span>{key}Ôºö</span>
                    <span>
                        {showEditValue ? (
                            <EditValue value={String(el)} onEdit={(val) => onEdit(itemKey, val)} />
                        ) : (
                            String(el)
                        )}
                    </span>

                    {/* ÈÖçÁΩÆÁâπÊÆäÊ†°È™å */}
                    <span style={{ display: showConfigSpecialVerif ? '' : 'none', marginLeft: 5 }}>
                        <ConfigSpecialVerif
                            keyPath={convertChainPathToJsonPath([itemKey])?.[0]}
                            value={String(el)}
                            specialAssertConfigs={editorData?.[rootNode]?.specialAssertConfigs ?? []}
                            onConfirm={(vals: any) => {
                                handleSpecialConfig(vals)
                            }}
                        />
                    </span>

                    <span style={{ marginLeft: 5, display: showAssertChecked ? '' : 'none' }}>
                        <Checkbox
                            checked={assertKeys?.includes(itemKey)}
                            onChange={(e) => {
                                onCheckbox(e.target.checked, itemKey)
                            }}
                        >
                            <span style={{ color: 'red' }}>{noShowAssert ? '' : 'Assert'}</span>
                        </Checkbox>

                    </span>
                </div>
            ) : (
                <div>
                    <span>{key}</span>

                    <span
                        style={{
                            marginLeft: 15,
                            display: ['editData'].includes(itemKey) && isAllAssert && showAssertChecked ? '' : 'none',
                        }}
                    >
                        <Checkbox
                            style={{ visibility: isAllAssert ? 'visibility' : 'hidden' as any }}
                            checked={allCheckedKeys?.length === assertKeys?.length}
                            indeterminate={0 < assertKeys?.length && assertKeys?.length < allCheckedKeys?.length}
                            onChange={(e) => selectAll(e.target.checked)}
                        >
                            <span>ÂÖ®ÈÄâ</span>
                        </Checkbox>
                    </span>
                </div>
            );
            return {
                key: itemKey,
                title: title,
                children: isBasicType ? null : convertToAntdTreeData(el, itemKey, depth + 1), // Âü∫Êú¨Êï∞ÊçÆÁ±ªÂûãÊ≤°ÊúâÂ≠êËäÇÁÇπ
            };
        };

        if (Array.isArray(data) && data?.length > 0) {
            // Â§ÑÁêÜÊï∞ÁªÑÁ±ªÂûãÁöÑÊï∞ÊçÆ
            return data.map((el, index) => {
                const key = index; // Êï∞ÁªÑÁöÑ key ‰∏∫ÂÆÉÁöÑÁ¥¢Âºï
                if (typeof el === 'object') {
                    return buildTreeNode(el, key);
                } else {
                    return buildTreeNode(el, key, true); // Âü∫Êú¨Êï∞ÊçÆÁ±ªÂûãÁõ¥Êé•ÊòæÁ§∫ÂÄº
                }
            });
        } else if (data !== null && typeof data === 'object') {
            // Â§ÑÁêÜÂØπË±°Á±ªÂûãÁöÑÊï∞ÊçÆ
            return Object.keys(data).map((key) => {
                const el = data[key];
                const isBasicType = !(el !== null && typeof el === 'object');
                return buildTreeNode(el, key, isBasicType);
            });
        } else {
            // Â§ÑÁêÜÊ†πËäÇÁÇπ‰∏∫Âü∫Êú¨Êï∞ÊçÆÁ±ªÂûãÁöÑÊÉÖÂÜµ
            return [
                {
                    key: nodeKey,
                    title: `${nodeKey}: ${data}`,
                },
            ];
        }
    };
    const treeData = useMemo(() => convertToAntdTreeData(renderData), [renderData]);

    return (
        <div style={{ width: '100%', overflowX: 'auto' }}>
            {renderData && (
                <Tree
                    defaultExpandedKeys={allCheckedKeys}
                    treeData={treeData}
                    virtual
                />
            )}

        </div>
    );
};

export default CustomEditor;